
body, html {
font-family: "Palanquin";
font-weight: normal;
font-style: normal;
}
h1{
font-family: "Merriweather";
font-weight: 900;
font-style: normal;
}
h2, h3, h4 {
font-family: "Kadwa";
font-weight: normal;
font-style: normal;
}
@keyframes flickerAnimation {
0%   { opacity:1;
}
50%  { opacity:0;
}
100% { opacity:1;
}
}
@-webkit-keyframes flickerAnimation{
0%   { opacity:1;
}
50%  { opacity:0;
}
100% { opacity:1;
}
}
.flicker {
   -webkit-animation: flickerAnimation 2s infinite;
    animation: flickerAnimation 2s infinite;
}
#app {
}
div#preloader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #fff url('./assets/loading.gif') no-repeat center center;
}
#preloader h1 {
  text-align: center;
  padding-top: 100px;
}



